body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.speaker .name font {
  font-weight: bold;
  size: 22px; }

.speaker .position font {
  size: 12px; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

.details {
  background-color: #03A9F4; }

.details {
  width: 100%;
  height: 85vh;
  overflow: auto;
  padding: 0 20px;
  margin-top: 15px;
  color: #0a0002;
  border-radius: 10px; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.weatherDataCont {
    background-color: rgba(200, 200, 200, 0.5);
    width: 100%;
    height: 100px;
    line-height: 100px;
    border-radius: 5px;
    padding: 10px 25px;
}

.weatherExtraInfoCont {
    width: 40%;
    display: inline-block;
    line-height: 28px;
    text-align: left;
}

.weatherTemperatureCont {
    display: inline-block;
    width: 60%;
}

.temperature {
    font-size: 20px;
    font-weight: bold;
    padding-left: 20px;
    color: #333;
}

.temperatureType {
    font-weight: bold;
    color: #666;
}

.extraInfoText {
    display: block;
    color: #1535c5;
    font-size: 16px;
    font-weight: bold;
}

.wicon {
    color: rgb(255, 72, 0)
}
.locationCont {
    width: 100%;
    height: 35px;
    text-align: left;
    color: #888;
    line-height: 35px;
    overflow: hidden;
}

.locationCont h1 {
    margin: 0;
    white-space: nowrap;
}

.weatherLocationCont {
    cursor: pointer;
    width: calc(100% - 20px);
    margin: 20px 10px;
    min-width: 350px;
    padding: 25px;
    font-family: "Roboto", sans-serif;
    box-shadow: 10px 10px 40px -12px rgba(0, 0, 0, 0.5)
}
.details h2 {
    text-align: center;
    padding: 20px 0px;
    color: white;
    margin: 0px;
}

.locationList {
    margin-top: 15px;
    overflow: auto;
    height: 85vh;
}

@media (max-width:768px) {
    .locationList {
        height: 40vh;
        overflow: auto;
        margin-bottom: 20px;
    }
}
.forecastItemsWrapper {
    margin-bottom: 20px;
}

.forecastItemDate {
    font-weight: bold;
}
