.locationCont {
    width: 100%;
    height: 35px;
    text-align: left;
    color: #888;
    line-height: 35px;
    overflow: hidden;
}

.locationCont h1 {
    margin: 0;
    white-space: nowrap;
}

.weatherLocationCont {
    cursor: pointer;
    width: calc(100% - 20px);
    margin: 20px 10px;
    min-width: 350px;
    padding: 25px;
    font-family: "Roboto", sans-serif;
    box-shadow: 10px 10px 40px -12px rgba(0, 0, 0, 0.5)
}